*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App{
  padding-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  .top_part{
    img{
      width: 300px;
    object-fit: cover;
    }
  }
}

.LangCard {
  width: 150px;
  height: 110px;
  background-color: wheat;
  border-radius: 15px;
  position: relative;
  transition: all 0.3s ease;
  font-family: 'Itim', cursive;

  &:hover {
    box-shadow: 1px 0px 11px 2px rgba(0, 0, 0, 0.75);
    transition: all 0.3s ease;
    .logoimg{
      transform: scale(1.3);
      transition: all 0.3s ease;
    }
  }
}

.back{
  width: 100%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  padding-left: 10px;

  ul{
    li{
      list-style: inside;
    }
  }
}
.logo{
  position: relative;
  top: 15px;
  width: 100%;
  display: flex;
  justify-content: center;
  img{
    width: 45%;
    transition: all 0.3s ease;
  }
}

.name {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 6px;
  text-align: center;
}

.ContainerCard {
  display: grid;
  border-radius: 15px;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 50px repeat(4, 1fr);
  gap: 10px;
  padding: 15px;
  background-image: linear-gradient(
    30deg,
    #f58a28 0%,
    #f58a28 40%,
    #54c3df 40%,
    #54c3df 60%,
    #f58a28 100%,
  );

  h2 {
    text-align: center;
    grid-column: 1 / span 2;
    color: white;
    font-size: 30px;
  }
}